.archive {
  font-size: 1.125rem;
}

.archive__wrapper {
  flex-direction: column;
  display: flex;
}

.archive__wrapper a {
  color: #fff;
  text-decoration: none;
}

.archive__wrapper a :hover {
  color: #ffffff80;
}

.archive__grid {
  border-bottom: 1px solid #ffffff1a;
  grid-template-rows: auto;
  grid-template-columns: .4fr .8fr 2fr .1fr;
  padding: 1rem 0 .5rem;
  display: grid;
}

@media (width <= 640px) {
  .archive__grid {
    grid-template-rows: auto;
    grid-template-columns: 1fr .1fr;
    row-gap: .5rem;
    line-height: 110%;
  }

  .archive__grid-item:first-child, .archive__grid-item:nth-child(2), .archive__grid-item:nth-child(3) {
    grid-column: 1 / span 1;
  }

  .archive__grid-item:nth-child(4) {
    grid-area: 1 / 2 / span 1 / span 1;
    justify-content: flex-end;
    align-items: flex-start;
    display: flex;
  }
}

.header__archive {
  text-align: center;
  margin-top: 100px;
}

